.ck-main {
  --text-color: white;
  --bg-color: #292c35;
  --secondary-bg-color: black;
  --border-color: grey;
  --active-text-color: #316cb3;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
    Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ck-main code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.ck-main {
  background-color: var(--bg-color);
  min-height: 400px;
  min-width: 500px;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 50px 1fr 20px;
  grid-template-columns: 200px 1fr;
  border: solid var(--border-color) 1px;
}
.ck-short {
  border: solid var(--border-color) 1px;
  grid-template-columns: 56px 1fr;

}
/* .ck-short.ck-editor {
  border-left: none;
} */
.ck-toolbar {
  grid-row: 1;
  grid-column: 1/4;
  padding: 7px;
  border-bottom: solid var(--border-color) 1px;
  display: flex;
  justify-content: space-between;
}

/* .ck-menus {
  grid-row: 2;
  grid-column: 1;
  border-right: solid var(--border-color) 1px;
} */
/* directory : second left sidebar*/

.ck-directory {
  grid-row: 2;
  grid-column: 1;
}

/* editor : text editor */

.ck-editor {
  grid-row: 2;
  grid-column: 2;
  border-left: solid var(--border-color) 1px;
}

/* footer */

.ck-footer {
  grid-row: 3;
  border-top: solid var(--border-color) 1px;
  grid-column: 1/4;
}

.ck-add-user {
  grid-row: 2;
  grid-column: 2;
  border-left: solid var(--border-color) 1px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 300px;
}

.ck-add-user .ck-user-container {
  display: flex;
  justify-content: center;
  color: white;
}
.ck-user-box{
  width: 300px;
  text-align: right;
}
.ck-user-box button {
  padding: 5px 20px;
  text-decoration: none;
  color: var(--text-color);
  background-color: var(--active-text-color);
  border: none;
  height: 47px;
  cursor: pointer;
  font-size: 17px;
  border-radius: 4px 10px 10px 4px;

}

.ck-user-box input {
  padding: 5px 22px;
  padding-right: 0px;
  height: 30px;
  border-radius: 35px 0px 0px 35px;
  font-size: 17px;
  border: solid 1px;
  border-right: 0px;
  width: 140px;
  border : none;
  box-shadow: 0px 0px 15px 1px rgba(255, 255, 255, 0.2);
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}

.ck-user-box input:focus {
  outline: none;
  max-width: width calc(100% - 75px);
  width : 180px;
  box-shadow: 0px 0px 20px 1px rgba(255, 255, 255, 0.5);
}

.ck-logo h2 {
  padding: 0px;
  margin: 0px;
  font-size: 2rem;
  color: var(--active-text-color);
  transform: scale(1, 1.3);
  font-weight: bold;
  font-family: monospace;
  text-shadow: 2px 3px #691717;
  padding: 0 10px;
}

.ck-toolbar select {
  height: 34px;
  background-color: white;
  border-radius: 4px;
  padding-left: 10px;
  font-size: 14px;
}
.firepad {
  width: 100%;
  height: 100%;
  display: flex;
}

#aceEditor {
  font-size: 14px;
  background-color: var(--bg-color);
  display: flex;
  width: 100%;
  height: 100%;
  line-height: 1ck-3;
}

.ck-user-group {
  border-radius: 0px;
  margin: 0;
  padding: 0;
}
.ck-user-list {
  background-color: var(--bg-color) !important;
  color: white !important;
  padding: 5px 10px;
  display: flex;
  /* justify-content: space-between; */
}
.ck-user-circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: solid;
  color: var(--active-text-color);
  font-size: 17px;
  line-height: 1.6;
  border-color: white;
  font-weight: bold;
  text-align: center;
}

.ck-username {
  font-size: 16px;
  padding-left: 10px;
  line-height: 2;
  padding: 3px;
}
.ck-short .ck-username{
  display: none; 
}
.ck-me {
  background-color: var(--active-text-color);
  color: white;
  padding: 3px;
  border-radius: 10px;
}

.ck-error {
  color: #ff3939;
  padding: 5px 0px;
  height: 25px;
}
.ck-toggle{
  border-bottom:solid var(--border-color) 1px;
  cursor: pointer;
}
.ck-toggle .icon{
  font-size: 38px;
  line-height: 0.6;
}
